// Pagination
//
// Pagination is used to navigate between lists of products or records.
// Pagination is generally accompanied by a results counter.
//
// Markup:
// <nav>
//   <p class="pagination-count">1-12 of 37 results</p>
//   <ul class="pagination-links">
//     <li class="pagination-prev">
//       <a href="#">
//         <i class="icon-chevron-left"></i>
//       </a>
//     </li>
//     <li><a href="#">1</a></li>
//     <li><a class="pagination-active" href="#">2</a></li>
//     <li><a href="#">3</a></li>
//     <li><a href="#">...</a></li>
//     <li><a href="#">9</a></li>
//     <li><a href="#">10</a></li>
//     <li class="pagination-next">
//       <a href="#">
//         <i class="icon-chevron-right"></i>
//       </a>
//     </li>
//   </ul>
// </nav>
//
// Styleguide 3.6

.pagination-links {
	@extend .clearfix;
	float: left;

	li {
		float: left;
		height: $sc-target-size;
		width: $sc-target-size;
		text-align: center;
		border: 1px solid #c6c6c6;
		border-radius: 50%;
		margin: 0 2px;
		&:hover {
			background: #282828;
			a {
				color: #fff;
			}
		}
	}

	a {
		display: block;
		text-decoration: none;
		padding-left: $sc-padding-lv3;
		padding-right: $sc-padding-lv3;
		height: $sc-target-size;
		line-height: $sc-target-size;
	}
}

.pagination-count {
	padding: $sc-padding-lv2 $sc-padding-lv4;
	float: left;
}

.pagination-links-number {
	display: none;
/*	background-color: $sc-color-theme-background;*/

	@media (min-width: $screen-md-min) {
		display: block;
	}
}

.pagination-prev,
.pagination-next {
	/*background-color: $sc-color-theme-background;*/
	border: 0.5px solid #282828;
	border-radius: 50%;
	a:hover{
		/*background-color: $sc-color-theme-background;*/
	}
}

.pagination-prev,


.pagination-next,
.pagination-next-disabled {
	margin-left: $sc-margin-lv1;

	@media (min-width: $screen-md-min) {
		margin-left: 0;
	}
}

.pagination-disabled,
.pagination-prev-disabled,
.pagination-next-disabled {
	display:none;
}

.pagination-active {
	color: #000;
	cursor: default;

	&:hover{
		color: #121212;
	}
}
